import axios from "axios";

export const GET_ALL_DOGS = "GET_ALL_DOGS";
export const GET_DOG_DETAIL = "GET_DOG_DETAIL";
export const CREATE_DOG = "CREATE_DOG";
export const DELETE_DOG = "DELETE_DOG";

const MY_API_KEY = "live_wving1yDkC3YlOzVElwQcw56xtc1kWkEqfAS3bs70xKsjTkzWuO7cznfzqIYQ8M1";

export const getAllDogs = () => {
    return(dispatch) => {
        return axios.get(`https://api.thedogapi.com/v1/breeds?api_key=${MY_API_KEY}`)
            .then(res => dispatch({
                type: GET_ALL_DOGS,
                payload: res.data
            }))
    }
};

export const getDogDetail = (id) => {
    return (dispatch) => {
        return axios.get(`https://api.thedogapi.com/v1/breeds/${id}?api_key=${MY_API_KEY}`)
            .then(res => dispatch ({
                type: GET_DOG_DETAIL,
                payload: res.data
            }))
    }
};

var idDog = 0;

export const createDog = (payload) => {
    return {
        type: CREATE_DOG,
        payload: {
            ...payload,
            id: idDog++,
        }
    }
};

export const deleteDog = (payload) => {
    return {
        type: DELETE_DOG,
        payload,
    }
};